.checkbox {
    background-color: #fff;
    display: inline-block;
    height: 20px;
    margin: 0 20px;
    width: 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
}

.checkbox span {
    display: block;
    position: relative;
    bottom: 4px;
    right: 3px;
}



.checkbox span:after {
    -webkit-transform: scaleX(-1) rotate(135deg);
    transform: scaleX(-1) rotate(135deg);
    -webkit-transform-origin: left top;
    transform-origin: left top;
    border-right: 4px solid #fff;
    border-top: 4px solid #fff;
    content: '';
    display: block;
    height: 18px;
    left: 3px;
    position: absolute;
    top: 15px;
    width: 10px
}

.checkbox input {
    display: none
